<template>
	<div id="sidebar" class="sidebar responsive ace-save-state">
		<va-sidebar-btns></va-sidebar-btns>

		<ul class="nav nav-list">
			<va-sidebar-item v-for="(item,index) in sidemenu" :key="index" :item="item"></va-sidebar-item>
		</ul>
	
		<div class="sidebar-toggle sidebar-collapse" id="sidebar-collapse">
			<i id="sidebar-toggle-icon" class="ace-icon fa fa-angle-double-left ace-save-state" data-icon1="ace-icon fa fa-angle-double-left" data-icon2="ace-icon fa fa-angle-double-right"></i>
		</div>
	</div>
</template>

<script>
	import _sidemenu from '@/router/sidemenu.js'
	import _sitebar_item from '@/components/sidebar_item.vue'
	import _sitebar_quick_btns from '@/widgets/sidebar_quick_buttons.vue'

  	export default {
		name: 'sidebar',
		components: {
			'va-sidebar-item': _sitebar_item,
			'va-sidebar-btns': _sitebar_quick_btns
		},
		data() {
			return {
				sidemenu: _sidemenu,
			}
		},
		mounted: function() {
			try { ace.settings.loadState('sidebar') } catch (e) {}
		},
		watch: {
			'$route' (to, from) {
				// clear current state
				$("#sidebar .open").removeClass('open')
				$("#sidebar .active").removeClass('active')

				// set parent open
				var $t = $('#sidebar a[href="' + to.path + '"]')
				if ($t) {
					if ($t.parent().find('ul')) {
						if ($t.parent().is("li")) {
							$t.parent().addClass('active')
						}
					} else {
						var $n = $t.parent().parent().parent()
						if ($n.is("li")) {
							$n.addClass('active open')
						}
					}
				}
			}
		},
	}
</script>

<style>
</style>



